@use './colors';

.contact_card {
  width: 700px;
  height: 500px;
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  box-shadow: 0.5px 0.5px 10px colors.$darkBlue;
}

.first_card,
.second_card {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.first_card {
  position: relative;
  background-color: transparent;
  h3 {
    position: absolute;
    top: 15px;
  }
}

.contact_input {
  font: inherit;
  height: 35px;
  width: 80%;
  margin-top: 20px;
  font-weight: 500;
  font-size: 12px;
  opacity: 90%;
  background-color: transparent;
  transition: all 0.3s;
  padding: 7px;
  outline: 0;
  border: none;
  border-bottom: 2px solid colors.$darkOrange;
  color: white;
}

.submit_button {
  margin-top: 50px;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
  border: none;
  padding: 0;
  background-color: colors.$darkOrange;
  color: colors.$white;
  cursor: pointer;
  &:hover {
    opacity: 0.8;
    transform: scale(1.06);
    transition: ease-in-out 0.25s;
  }
}

.after_submit_button {
  opacity: 0.3;
  &:hover {
    opacity: 0.3;
    transform: scale(1);
    transition: ease-in-out 0.25s;
  }
}

#confirmal_message {
  margin-top: 10px;
  color: #77db2c;
  opacity: 0;
}

#error_message {
  margin-top: 10px;
  color: #db2c2c;
  opacity: 0;
}

.display {
  opacity: 1 !important;
}

.text {
  height: 100px;
  resize: none;
}

.copy {
  display: none;
}

//SECOND PART OF CARD

.second_card {
  background: colors.$cardGradient;
  color: colors.$white;
  position: relative;
  h3 {
    position: absolute;
    top: 15px;
  }
  ul {
    list-style: none;
  }
  li {
    padding: 10px;
  }
  span {
    padding-right: 10px;
    color: colors.$white;
  }
  a {
    color: colors.$darkBlue;
    &:hover {
      text-decoration: underline;
      color: colors.$white;
    }
  }
}

//Mobile view

@media (max-width: 800px) {
  .contact_card {
    display: flex;
    flex-direction: column;
    width: 450px;
    height: 550px;
    margin: 0 auto;
    padding-top: 25px;
    margin-bottom: 200px;
  }

  .second_card {
    display: none;
  }

  .first_card {
    margin: 0 auto;
    width: 100%;
    h3 {
      position: relative;
      margin-bottom: 25px;
    }
  }

  .copy {
    display: flex;
    width: 450px;
    height: 350px;
    margin-top: 50px;
  }
}
